'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { useRouter } from 'next/navigation';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Badge } from '@/components/ui/badge';
import { getStudentResults, saveStudentResult, deleteStudentResult, StudentResult } from '@/lib/mockData';

export default function ResultsPage() {
  const { user, isLoading } = useAuth();
  const router = useRouter();
  const [results, setResults] = useState<StudentResult[]>([]);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [formData, setFormData] = useState({
    studentName: '',
    subject: '',
    marks: '',
    maxMarks: '100',
    comments: ''
  });

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login');
      return;
    }

    if (user) {
      loadResults();
    }
  }, [user, isLoading, router]);

  const loadResults = () => {
    const allResults = getStudentResults();
    setResults(allResults);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    setIsSubmitting(true);

    // Validation
    if (!formData.studentName.trim() || !formData.subject.trim() || !formData.marks.trim()) {
      setError('Please fill in all required fields');
      setIsSubmitting(false);
      return;
    }

    const marks = parseFloat(formData.marks);
    const maxMarks = parseFloat(formData.maxMarks);

    if (isNaN(marks) || isNaN(maxMarks) || marks < 0 || maxMarks <= 0 || marks > maxMarks) {
      setError('Please enter valid marks (marks should be between 0 and max marks)');
      setIsSubmitting(false);
      return;
    }

    try {
      const success = saveStudentResult({
        studentName: formData.studentName.trim(),
        subject: formData.subject.trim(),
        marks,
        maxMarks,
        comments: formData.comments.trim(),
        postedBy: user?.name || 'Unknown'
      });

      if (success) {
        setSuccess('Result posted successfully!');
        setFormData({
          studentName: '',
          subject: '',
          marks: '',
          maxMarks: '100',
          comments: ''
        });
        loadResults();
      } else {
        setError('Failed to save result. Please try again.');
      }
    } catch (err) {
      setError('An error occurred while saving the result.');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleDelete = (id: string) => {
    if (window.confirm('Are you sure you want to delete this result?')) {
      const success = deleteStudentResult(id);
      if (success) {
        setSuccess('Result deleted successfully!');
        loadResults();
      } else {
        setError('Failed to delete result.');
      }
    }
  };

  const getGradeColor = (percentage: number) => {
    if (percentage >= 90) return 'bg-green-100 text-green-800';
    if (percentage >= 80) return 'bg-blue-100 text-blue-800';
    if (percentage >= 70) return 'bg-yellow-100 text-yellow-800';
    if (percentage >= 60) return 'bg-orange-100 text-orange-800';
    return 'bg-red-100 text-red-800';
  };

  const getGrade = (percentage: number) => {
    if (percentage >= 90) return 'A+';
    if (percentage >= 80) return 'A';
    if (percentage >= 70) return 'B';
    if (percentage >= 60) return 'C';
    if (percentage >= 50) return 'D';
    return 'F';
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-lg">Loading...</div>
      </div>
    );
  }

  if (!user) {
    return null;
  }

  const canPostResults = user.role === 'admin' || user.role === 'teacher';

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="space-y-2">
        <h1 className="text-3xl font-bold text-gray-900">Student Results</h1>
        <p className="text-gray-600">
          {canPostResults 
            ? 'Post and manage student academic results' 
            : 'View student academic results'
          }
        </p>
      </div>

      {/* Post Result Form (for admin and teachers only) */}
      {canPostResults && (
        <Card>
          <CardHeader>
            <CardTitle>Post New Result</CardTitle>
            <CardDescription>
              Add a new academic result for a student
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="studentName">Student Name *</Label>
                  <Input
                    id="studentName"
                    type="text"
                    placeholder="Enter student name"
                    value={formData.studentName}
                    onChange={(e) => setFormData(prev => ({ ...prev, studentName: e.target.value }))}
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="subject">Subject *</Label>
                  <Input
                    id="subject"
                    type="text"
                    placeholder="Enter subject name"
                    value={formData.subject}
                    onChange={(e) => setFormData(prev => ({ ...prev, subject: e.target.value }))}
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="marks">Marks Obtained *</Label>
                  <Input
                    id="marks"
                    type="number"
                    placeholder="Enter marks"
                    min="0"
                    step="0.1"
                    value={formData.marks}
                    onChange={(e) => setFormData(prev => ({ ...prev, marks: e.target.value }))}
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="maxMarks">Maximum Marks</Label>
                  <Input
                    id="maxMarks"
                    type="number"
                    placeholder="Enter maximum marks"
                    min="1"
                    value={formData.maxMarks}
                    onChange={(e) => setFormData(prev => ({ ...prev, maxMarks: e.target.value }))}
                    required
                  />
                </div>
              </div>

              <div className="space-y-2">
                <Label htmlFor="comments">Comments</Label>
                <Textarea
                  id="comments"
                  placeholder="Enter any comments or feedback"
                  value={formData.comments}
                  onChange={(e) => setFormData(prev => ({ ...prev, comments: e.target.value }))}
                  rows={3}
                />
              </div>

              {error && (
                <div className="text-sm text-red-600 bg-red-50 p-3 rounded-md">
                  {error}
                </div>
              )}

              {success && (
                <div className="text-sm text-green-600 bg-green-50 p-3 rounded-md">
                  {success}
                </div>
              )}

              <Button type="submit" disabled={isSubmitting}>
                {isSubmitting ? 'Posting Result...' : 'Post Result'}
              </Button>
            </form>
          </CardContent>
        </Card>
      )}

      {/* Results List */}
      <Card>
        <CardHeader>
          <CardTitle>All Results</CardTitle>
          <CardDescription>
            {results.length === 0 
              ? 'No results posted yet' 
              : `${results.length} result${results.length === 1 ? '' : 's'} found`
            }
          </CardDescription>
        </CardHeader>
        <CardContent>
          {results.length === 0 ? (
            <div className="text-center py-8 text-gray-500">
              <p>No results have been posted yet.</p>
              {canPostResults && (
                <p className="mt-2">Use the form above to post the first result.</p>
              )}
            </div>
          ) : (
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Student Name</TableHead>
                    <TableHead>Subject</TableHead>
                    <TableHead>Marks</TableHead>
                    <TableHead>Percentage</TableHead>
                    <TableHead>Grade</TableHead>
                    <TableHead>Posted By</TableHead>
                    <TableHead>Date</TableHead>
                    {canPostResults && <TableHead>Actions</TableHead>}
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {results.map((result) => {
                    const percentage = (result.marks / result.maxMarks) * 100;
                    const grade = getGrade(percentage);
                    
                    return (
                      <TableRow key={result.id}>
                        <TableCell className="font-medium">{result.studentName}</TableCell>
                        <TableCell>{result.subject}</TableCell>
                        <TableCell>{result.marks}/{result.maxMarks}</TableCell>
                        <TableCell>{percentage.toFixed(1)}%</TableCell>
                        <TableCell>
                          <Badge className={getGradeColor(percentage)}>
                            {grade}
                          </Badge>
                        </TableCell>
                        <TableCell>{result.postedBy}</TableCell>
                        <TableCell>
                          {new Date(result.datePosted).toLocaleDateString()}
                        </TableCell>
                        {canPostResults && (
                          <TableCell>
                            <Button
                              variant="destructive"
                              size="sm"
                              onClick={() => handleDelete(result.id)}
                            >
                              Delete
                            </Button>
                          </TableCell>
                        )}
                      </TableRow>
                    );
                  })}
                </TableBody>
              </Table>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Results with Comments */}
      {results.some(result => result.comments) && (
        <Card>
          <CardHeader>
            <CardTitle>Results with Comments</CardTitle>
            <CardDescription>
              Detailed feedback and comments from teachers
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {results
                .filter(result => result.comments)
                .map((result) => {
                  const percentage = (result.marks / result.maxMarks) * 100;
                  const grade = getGrade(percentage);
                  
                  return (
                    <div key={result.id} className="border rounded-lg p-4">
                      <div className="flex items-center justify-between mb-2">
                        <div className="flex items-center space-x-3">
                          <h3 className="font-semibold">{result.studentName}</h3>
                          <Badge variant="outline">{result.subject}</Badge>
                          <Badge className={getGradeColor(percentage)}>
                            {grade} ({percentage.toFixed(1)}%)
                          </Badge>
                        </div>
                        <span className="text-sm text-gray-500">
                          {new Date(result.datePosted).toLocaleDateString()}
                        </span>
                      </div>
                      <p className="text-gray-700 mb-2">{result.comments}</p>
                      <p className="text-sm text-gray-500">- {result.postedBy}</p>
                    </div>
                  );
                })}
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
